.Contacts {
    height: 100vh;
    width: 100vw;
    /* background-color: #ffffff; */
    /* background-color: #009b8c7c; */
    background-image: url('https://s25.picofile.com/file/8451585292/tina_dawson_Kim9COAIEGc_unsplash_5922e7ed1d3da93bbae6.jpg');
    background-position: center;
    background-size: cover;
}

.blure {
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: #009b8c4d;
}

.addIcon-Contacts {
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: #009b8cd8;
    border-radius: 50%;
    bottom: 10px;
    right: 10px;
    background-image: url('https://s24.picofile.com/file/8451585050/add_user_a3f9f4bc09c16e79a6b8.png');
    background-size: 55%;
    background-position: center;
    background-repeat: no-repeat;
    border: 3pt solid #d7ecffa2;
}

.refresh {
    margin-left: 10px;
}

.refresh .icon-refresh {
    /* background-color: red; */
    width: 40px;
    height: 40px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin: 0 auto;
    background-image: url('https://s24.picofile.com/file/8451585918/sync_FILL0_wght400_GRAD0_opsz48.png');
    background-position: center;
    background-size: 80%;
    background-repeat: no-repeat;
}

