    .SmsPart {
        width: 98%;
        background-color: rgba(255, 255, 255, 0.548);
        display: block;
        margin: 10px auto;
        padding: 2px;
        overflow: hidden;
        border-radius: 25px;
        -webkit-border-radius: 25px;
        -moz-border-radius: 25px;
        -ms-border-radius: 25px;
        -o-border-radius: 25px;
        border: 2pt solid #d7ecffa2;
        z-index: 2;
        padding-bottom: 10px;
    }
    
    #part1-SmsPart {
        width: 100%;
        margin-top: 5px;
        /* justify-content: space-between; */
    }
    
    .icon-SmsPart {
        width: 50px;
        height: 50px;
        /* background-color: bisque; */
        background-image: url('https://s24.picofile.com/file/8451585218/speech_bubble_0818ce581bc0f883efdc.png');
        background-position: center;
        background-size: 60%;
        background-repeat: no-repeat;
        border-radius: 50%;
        border: 2pt solid #d7ecff;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        float: right;
    }
    
    .description-SmsPart {
        /* background-color: chartreuse; */
        width: 60%;
        text-align: right;
        float: right;
        margin-top: 10px;
        margin-right: 7px;
        /* margin-top: 50px; */
    }
    
    .description-SmsPart p:nth-child(1) {
        font-size: large;
        font-weight: 600;
    }
    
    .open-SmartPart {
        width: 25px;
        height: 25px;
        float: left;
        background-color: #009b8b;
        background-image: url('https://s25.picofile.com/file/8451586092/arrow_down_sign_to_navigate.png');
        background-position: center;
        background-size: 70%;
        background-repeat: no-repeat;
        border-radius: 10px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        -ms-border-radius: 10px;
        -o-border-radius: 10px;
        border: 2pt solid #009b8b;
        margin-top: 8px;
        margin-left: 5px;
        box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    }
    
    #part2-SmsPart {
        position: relative;
        width: 95%;
        /* height: 60px; */
        /* background-color: red; */
        margin: 70px auto;
    }
    
    #part2-SmsPart .input-SmsPart {
        width: 100%;
        /* height: 70px; */
        /* background-color: aqua; */
        margin: 10px auto;
    }
    
    #part2-SmsPart .input-SmsPart input {
        width: 98%;
        height: 120px;
        border-radius: 20px;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
        border: 2pt solid #d7ecff;
        background-color: #ffffff00;
        text-align: right;
    }
    
    #part2-SmsPart .controllers-SmsPart {
        margin: 16px auto;
        width: 100%;
        /* background-color: green; */
        display: flex;
        justify-content: space-between;
    }
    
    #part2-SmsPart .controllers-SmsPart button {
        width: 40%;
        height: 35px;
        background-color: #009b8b;
        border-radius: 15px;
        border: 2pt solid #009b8b;
        font-size: large;
        font-weight: 600;
        -webkit-border-radius: 15px;
        -moz-border-radius: 15px;
        -ms-border-radius: 15px;
        -o-border-radius: 15px;
        box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    }
