.Charge {
    height: 100vh;
    width: 100vw;
    /* background-color: #ffffff; */
    background-color: #009b8c7c;
    font-size: large;
    font-weight: 600;
    background-image: url('https://s25.picofile.com/file/8451585292/tina_dawson_Kim9COAIEGc_unsplash_5922e7ed1d3da93bbae6.jpg');
    background-position: center;
    background-size: cover;
}

#part1-Charge {
    width: 95%;
    /* height: 65px; */
    /* background-color: red; */
    margin: 40px auto;
    padding: 5px;
}

#part1-Charge .cach-Charge {
    width: 150px;
    height: 150px;
    background-color: rgba(255, 255, 255, 0.548);
    border: 2pt solid rgb(255, 255, 255);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 20px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

#part1-Charge .increase-Charge {
    width: 100%;
    /* background-color: green; */
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
}

#part1-Charge .tell-Charge {
    margin-top: 30px;
    font-family: 'Vazirmatn', sans-serif;
    font-size: large;
    text-decoration: underline;
    letter-spacing: 1px;
    line-height: 45px;
    font-size: 15;
    font-weight: 600;
    padding: 5px;
    color: white;
}

#part1-Charge .increase-Charge input {
    width: 200px;
    height: 35px;
    border-radius: 60px;
    -webkit-border-radius: 60px;
    -moz-border-radius: 60px;
    -ms-border-radius: 60px;
    -o-border-radius: 60px;
    border: none;
    text-align: center;
    border: 2pt solid #009b8b;
    font-size: large;
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
}

#part1-Charge .increase-Charge button {
    width: 120px;
    height: 40px;
    background-color: #009b8b;
    border: 2pt solid #009b8b;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -ms-border-radius: 25px;
    -o-border-radius: 25px;
    font-size: medium;
    /* font-weight: 500; */
    font-weight: 600;
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
}