.Device {
    background-color: rgb(255, 192, 203);
    border-radius: 15px;
    overflow: hidden;
    margin-top: 5px;
    /* padding: 10px; */
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    /* border: 2pt solid #d7ecffa2; */
    font-size: medium;
    font-weight: 600;
    height: 100% !important;
}

.part2-Device .controller-Device {
    /* background-color: bisque; */
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.part2-Device .controller-Device .controllerBtns-Device {
    /* background-color: aquamarine; */
}

.part2-Device .controller-Device .controllerBtns-Device .icon-Device {
    /* background-color: red; */
    width: 65px;
    height: 65px;
    border-radius: 50%;
    border: 2pt solid #d7ecffa2;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.part2-Device .controller-Device .controllerBtns-Device #icon1-Device {
    background-image: url('https://s24.picofile.com/file/8451585126/no_call_52e089c855621b0e4876.png');
    background-position: center;
    background-size: cover;
    background-size: 55%;
    background-repeat: no-repeat;
}

.part2-Device .controller-Device .controllerBtns-Device #icon2-Device {
    background-image: url('https://s25.picofile.com/file/8451585300/unlock_1e9bd78a3f1b820ac6cd.png');
    background-position: center;
    background-size: cover;
    background-size: 55%;
    background-repeat: no-repeat;
}

.part2-Device .controller-Device .controllerBtns-Device #icon3-Device {
    background-image: url('./public/icons/openlock.png');
    background-position: center;
    background-size: 55%;
    background-repeat: no-repeat;
}

.part2-Device .controller-Device .controllerBtns-Device #icon4-Device {
    background-image: url('https://s25.picofile.com/file/8451585976/openlock.png');
    background-position: center;
    background-size: cover;
    background-size: 55%;
    background-repeat: no-repeat;
}

.part2-Device .controller-Device .controllerBtns-Device .text-Device {
    /* background-color: blue; */
    width: 75px;
}

.part1-Device .logo-Device img {
    /* background-color: blueviolet; */
    width: 98%;
    height: 200px;
}

.part1-Device .setting-Device {
    position: absolute;
    width: 35px;
    height: 35px;
    background-image: url('./public/icons/settings_FILL0_wght400_GRAD0_opsz48.png');
    background-position: center;
    background-size: cover;
    left: 10px;
    top: 10px;
}