.AddDevice {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: #009b8ca4;
    z-index: 3;
}

.AddDevice .centerPart-AddDevice {
    position: absolute;
    width: 95%;
    /*height: 20px;
    */
    background-color: white;
    border-radius: 15px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
}

.AddDevice .centerPart-AddDevice .titl-AddDevice {
    border-bottom: 2pt solid rgb(0, 155, 140);
    padding: 10px;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice {
    position: relative;
    text-align: right;
    /* background-color: yellow; */
    height: 400px;
    display: flex;
    flex-direction: column;
    padding: 15px;
    /* justify-content: end; */
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice div {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    /* background-color: chartreuse; */
    /* border-bottom: 2pt solid #009b8b; */
    /* padding-bottom: 5px; */
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice div input {
    width: 200px;
    height: 30px;
    border: 2px solid rgba(0, 0, 0, 0.534);
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice div div {
    margin-top: 0px;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice .part1Inp-AddDevice {
    /* position: absolute; */
    /* background-color: pink; */
    height: 40px;
    display: flex;
    flex-direction: row-reverse;
    right: 0px;
    z-index: 4;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice .part1Inp-AddDevice .selectModel-AddDevice {
    /* overflow: hidden; */
    /* overflow: visible; */
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice .part1Inp-AddDevice .selectModel-AddDevice div {
    /* background-color: blueviolet; */
    height: 40px;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice .part1Inp-AddDevice .selectModel-AddDevice ul {
    /* visibility: hidden; */
    /* background-color: chocolate; */
    overflow: hidden;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice .part1Inp-AddDevice .selectModel-AddDevice ul li {
    list-style: none;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice .part1Inp-AddDevice .selectModel-AddDevice {}

.AddDevice .centerPart-AddDevice .controller-AddDevice {
    display: flex;
    justify-content: space-around;
    padding: 15px;
}

.AddDevice .centerPart-AddDevice .controller-AddDevice button {
    width: 120px;
    height: 40px;
    background-color: #009b8b;
    border: #009b8b;
    border-radius: 30px;
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    -o-border-radius: 30px;
}

.AddDevice .centerPart-AddDevice .inputs-AddDevice .part2Inp-AddDevice span {
    /* position: relative; */
    /* top: 10px; */
    /* background-color: aqua; */
}

.part5Inp-AddDevice {
    /* position: absolute; */
    height: 40px;
    /* background-color: pink; */
    z-index: 3;
}

.part5Inp-AddDevice .selectModel-AddDevice {
    height: 40px;
}

.part5Inp-AddDevice ul {
    overflow: hidden;
}

.part6Inp-AddDevice {
    /* background-color: pink; */
    height: 40px;
    z-index: 2;
}

.part6Inp-AddDevice .selectModel-AddDevice {
    height: 40px;
}

.part6Inp-AddDevice ul {
    overflow: hidden;
}

.moreIcon-AddDevice {
    /* background-color: red; */
    height: 40px;
    width: 40px;
    background: url(./public/icons/d.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50%;
    background-color: #009b8b;
}

ul {
    background-color: #009b8b;
    border-end-end-radius: 10px;
    text-align: center;
}

ul li {
    margin-top: 7px;
    cursor: pointer;
    padding: 5px;
}

ul li:hover {
    background-color: rgb(173, 173, 173);
}

.selected-AddDevice {
    /* border: 2pt solid; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 10px;
    padding-right: 10px;
    background-color: #009b8b;
}

.titlInp-AddDevice {
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}