.OutPuts {
    width: 100vw;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.548);
    font-size: large;
    /* font-weight: 600; */
    background-image: url('https://s25.picofile.com/file/8451585292/tina_dawson_Kim9COAIEGc_unsplash_5922e7ed1d3da93bbae6.jpg');
    background-position: center;
    background-size: cover;
}

#part1-OutPuts {
    width: 97%;
    /* height: 10px; */
    /* background-color: red; */
    margin: 10px auto;
}

#part1-OutPuts .item-OutPuts {
    width: 100%;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.548);
    display: flex;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    overflow: hidden;
    margin-top: 10px;
    justify-content: center;
    border: 2pt solid white;
}

#part1-OutPuts .item-OutPuts .name-OutPuts,
#part1-OutPuts .item-OutPuts .state-OutPuts {
    width: 45%;
    height: 100%;
    /* background-color: aqua; */
    /* border: 2pt solid black; */
    /* flex-direction: row; */
    padding: 2px;
}

#part1-OutPuts .item-OutPuts .name-OutPuts {
    justify-content: right;
    text-align: right;
    display: flex;
}

#part1-OutPuts .item-OutPuts .state-OutPuts .inputPart-OutPuts {
    width: 65px;
    height: 30px;
    /* background-color: #cfd9e0; */
    border-radius: 35px;
    -webkit-border-radius: 35px;
    -moz-border-radius: 35px;
    -ms-border-radius: 35px;
    -o-border-radius: 35px;
    margin-top: 15px;
    margin-left: 15px;
    display: flex;
    justify-content: space-around;
    padding: 2px;
    /* border: 1pt solid #009b8b; */
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
}

#part1-OutPuts .item-OutPuts .state-OutPuts .inputPart-OutPuts span {
    /* background-color: red; */
    width: 20px;
    height: 30px;
    order: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

@keyframes state-off {
    0% {
        margin-left: 35px;
    }
    100% {
        margin-left: 5px;
    }
}

@keyframes state-on {
    0% {
        margin-left: 5px;
    }
    100% {
        margin-left: 35px;
    }
}

#part1-OutPuts .item-OutPuts .state-OutPuts .inputPart-OutPuts .input-OutPuts {
    position: relative;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: #ffffff;
    top: 2.5px;
}

#part1-OutPuts .item-OutPuts .name-OutPuts span p {
    /*height: 100%;
    */
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}