.Command {
    /* background-color: #009b8ca4; */
    position: fixed;
    bottom: 0px;
    /* left: 10px; */
    z-index: 2;
    margin: 0 auto;
    overflow: hidden;
    /* width: 95%; */
}

.icon-Command {
    position: absolute;
    width: 70px;
    height: 70px;
    background-image: url(./public/icons/received.png);
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
    background-color: #009b8cd8;
    border: 3pt solid #d7ecffa2;
    bottom: 5px;
    left: 5px;
    bottom: 5px;
    border-radius: 100%;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    -ms-border-radius: 100%;
    -o-border-radius: 100%;
}

.text-Command {
    position: absolute;
    width: 95%;
    /* height: 400px; */
    background-color: white;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    border-radius: 18px;
}

.text-Command textarea {
    width: 95%;
    height: 170px;
    padding: 5px;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    border: white;
    border: 2px solid rgba(0, 0, 0, 0.534);
    margin-top: 10px;
    text-align: right;
    flex-wrap: wrap;
}

.text-Command .titl-Command {
    width: 100%;
    border-bottom: 2pt solid #009b8b;
    padding: 10px;
    /* background-color: red; */
}

.text-Command button {
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: #009b8b;
    width: 120px;
    height: 35px;
    border: 2pt solid #009b8cc9;
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    /*margin: 0 auto;
    */
    border-radius: 50px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}