.ZoonInfo {
    width: 100vw;
    height: 100vh;
    background-color: #009b8c;
    font-size: large;
    /* font-family: 600; */
    background-image: url('https://s25.picofile.com/file/8451585292/tina_dawson_Kim9COAIEGc_unsplash_5922e7ed1d3da93bbae6.jpg');
    background-position: center;
    background-size: cover;
    overflow: hidden;
}

#part2-ZoonInfo {
    width: 100%;
    height: fit-content;
    /* border: 2pt solid #009b8c; */
    margin-top: 10px;
    overflow: hidden;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    overflow: hidden;
}

#part2-ZoonInfo .items-ZoonInfo {
    width: 97%;
    height: 66vh;
    /* background-color: pink; */
    margin: 0 auto;
    margin-top: 1px;
    /* background-color: red; */
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo {
    position: relative !important;
    background-color: rgba(255, 255, 255, 0.548);
    width: 100%;
    height: 46px;
    display: flex;
    justify-content: space-between;
    border-radius: 15px;
    overflow: hidden;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    border: 2pt solid white;
    margin-top: 8px;
    left: 50%;
    transform: translate(-50%);
    padding: 0px;
    -webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    padding-left: 5px;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .state-ZoonInfo,
#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .number-ZoonInfo,
#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .name-ZoonInfo,
#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .operator-ZoonInfo {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* background-color: red; */
    /* border: 2pt solid black; */
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .state-ZoonInfo {
    /* background-color: red; */
    display: flex;
    flex-direction: row;
    /* justify-content: space-between; */
    padding: 5px;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .operator-ZoonInfo {
    /* width: 42px; */
    height: 27px;
    overflow: hidden;
    background-color: #009b8c;
    position: relative;
    text-align: left;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    border-radius: 8px;
    -webkit-border-radius: 50;
    -moz-border-radius: 50;
    -ms-border-radius: 50;
    -o-border-radius: 50;
    border: 2pt solid #009b8c;
    /* box-shadow: 2pt 1pt 1pt rgba(255, 255, 255, 0.459); */
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    margin-right: 4px;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .state-ZoonInfo div {
    position: relative;
    text-align: left;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .state-ZoonInfo div p {
    position: relative;
    top: 50%;
    /* background-color: green; */
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    text-align: center;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .state-ZoonInfo div:nth-child(1) {
    /*background-color: rgb(250, 250, 162);
    */
    width: 100%;
    height: 27px;
    overflow: hidden;
    background-color: #009b8c;
    /* background-image: url('./public/icons/x.png');
    background-position: center;
    background-size: 70%;
    background-repeat: no-repeat; */
    /*margin-top: 2px;
    */
    border-radius: 8px;
    -webkit-border-radius: 50;
    -moz-border-radius: 50;
    -ms-border-radius: 50;
    -o-border-radius: 50;
    border: 2pt solid #009b8c;
    /* box-shadow: 2pt 1pt 1pt rgba(255, 255, 255, 0.459) */
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
}

.table-ZoonInfo .title-ZoonInfo {
    width: 100%;
    height: 6vh;
    /* background-color: orange; */
    display: flex;
    justify-content: space-between;
    border-bottom: 2pt solid white;
    font-size: large;
    font-weight: 600;
    color: white;
}

.table-ZoonInfo .title-ZoonInfo div {
    width: 30%;
    height: 100%;
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.table-ZoonInfo .title-ZoonInfo div p {
    /* background-color: aqua; */
}

.add-ZoonInfo {
    /* width: 19vh; */
    position: absolute;
    /* height: 19vh; */
    /* background-color: blueviolet; */
    padding: 5px;
    bottom: 5px;
    left: 5px;
    /* bottom: 2.5%; */
    /* margin-bottom: 0px; */
    /* float: inline-end; */
    /* background-color: #009b8cc0; */
}

.add-ZoonInfo .addIcon-ZoonInfo {
    width: 47px;
    height: 47px;
    /* /*background-color: red; */
    margin: 0 auto;
    background-image: url('https://s24.picofile.com/file/8451586034/plus_1_.png');
    background-position: center;
    background-size: 55%;
    background-repeat: no-repeat;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border: 3pt solid #d7ecffbe;
    float: left;
    background-color: #009b8c;
}