    .Page1 {
        width: 100vw;
        height: 100vh;
        background-image: url(https://s24.picofile.com/file/8451585250/thomas_lefebvre_mT1uxKaegSQ_unsplash_bde9b5fc5ab03a369e84.jpg);
        background-position: center;
        background-size: cover;
        overflow: scroll;
    }
    
    .part1-Page1 {
        width: 95%;
        margin: 0 auto;
        padding: 2pt;
        /* background-color: aqua; */
        height: 40%;
        overflow: hidden;
    }
    
    .part2-Page1 {
        height: 55%;
        overflow: scroll;
    }
    
    .part2-Page1 div {
        /*background-color: #d7ecff5e;
        */
        background-color: #009b8c7c;
        width: 95%;
        display: flex;
        flex-direction: row-reverse;
        margin: 10px auto;
        padding: 5px;
        padding-bottom: 3px;
        border-radius: 16px;
        border: 2pt solid #009b8b;
        -webkit-border-radius: 16px;
        -moz-border-radius: 16px;
        -ms-border-radius: 16px;
        -o-border-radius: 16px;
        /* height: 100%; */
    }
    
    .part2-Page1 div div {
        background-color: #d7ecff00;
        padding: 0px;
        margin: 0 0;
        width: auto;
        border-radius: 0px;
        /*background-color: aqua;*/
        -webkit-border-radius: 0px;
        -moz-border-radius: 0px;
        -ms-border-radius: 0px;
        -o-border-radius: 0px;
        border: none;
    }
    
    .part2-Page1 div .text-Page1 {
        /* background-color: aqua; */
        padding: 7px;
    }
    
    .part2-Page1 div .text-Page1 .title-Page1 p {
        font-size: medium;
        font-weight: 600;
    }
    
    .part2-Page1 div .icons-Page1 {
        padding: 5px;
    }
    
    .part2-Page1 div .icons-Page1 .icon-Page1 {
        width: 60px;
        height: 60px;
        /* background-color: #d7ecffa2; */
        border-radius: 50%;
        /* border: 2pt solid #009b8b; */
        border: 2pt solid #d7ecffa2;
        /* box-shadow: 1px 1px 1px 0.5px #d7ecff3a; */
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
    
    .part2-Page1 div .icons-Page1 #icon1-Page {
        background-image: url('https://s24.picofile.com/file/8451585992/empty_battery.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon2-Page {
        background-image: url('https://s25.picofile.com/file/8451585300/unlock_1e9bd78a3f1b820ac6cd.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon3-Page {
        background-image: url('https://s24.picofile.com/file/8451585242/telephone_55f60239547593a1b5d5.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon4-Page {
        background-image: url('https://s24.picofile.com/file/8451585068/alert_827e8970facbea46cdb0.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon5-Page {
        background-image: url('https://s24.picofile.com/file/8451585118/megaphone_b85bcf90c0f241d1ad4b.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon6-Page {
        background-image: url('https://s25.picofile.com/file/8451585134/plug_fe0b68b5246f871ccc35.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part1-Page1 .addDevice-Page1 {
        position: relative;
        width: 98%;
        height: 323px;
        margin-top: 5px;
        /*margin-right: 10px;
        */
        /* border: dashed #009b8b 2pt; */
        /* right: 2pt; */
    }
    
    .part1-Page1 .addDevice-Page1 .contentAdd-Page1 {
        position: absolute;
        width: 98%;
        height: 98%;
        background-color: #009b8c9d;
        border: 2pt dashed #003631;
        border-radius: 15px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
    }
    
    .part1-Page1 .contentAdd-Page1 .centerAdd-Page1 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        /* background-color: red; */
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
    }
    
    .part1-Page1 .contentAdd-Page1 .centerAdd-Page1 .iconAdd-Page1 {
        /*position: absolute;
        */
        width: 75px;
        height: 75px;
        margin: 0 auto;
        border: solid #003631 2pt;
        /*background-color: red;
        */
        /*left: 50%;
        */
        /*top: 50%;
        */
        border-radius: 50%;
        background-image: url(./public/icons/add\(1\).png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
    
    .part1-Page1 .contentAdd-Page1 .centerAdd-Page1 .textAdd-Page1 {
        margin-top: 20px;
    }