.Contact {
    width: 95%;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.548);
    display: flex;
    /* border-bottom: 1pt solid #009b8b; */
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    padding-right: 5px;
    margin: 0 auto;
    margin-top: 5px;
}

#part1-Contact {
    /* background-color: brown; */
    width: 60px;
    height: 60px;
}

#part1-Contact .addIcon-Contact {
    position: relative;
    width: 30px;
    height: 30px;
    background-image: url('https://s24.picofile.com/file/8451585084/bin_a6fd29509719f079bfd7.png');
    background-size: 60%;
    background-position: center;
    background-repeat: no-repeat;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}

#part2-Contact {
    /* background-color: cadetblue; */
    height: 60px;
    display: flex;
    width: 90%;
    flex-direction: row-reverse;
}

.icon-Contact {
    width: 50px;
    height: 50px;
    /*background-color: blueviolet;
    */
    background-image: url('https://s24.picofile.com/file/8451585818/user.png');
    background-size: 60%;
    background-position: center;
    background-repeat: no-repeat;
    /*padding: 5px;
    */
    border: 1.25pt solid #d7ecffa2;
    border-radius: 50%;
    margin-top: 2.5px;
    margin-right: 2.5px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.text-Contact {
    width: 75%;
    height: 60px;
    padding: 10px;
    /* background-color: darkorange; */
}

.text-Contact .title-Contact {
    width: 100%;
    /* background-color: red; */
    text-align: right;
    font-size: large;
    font-weight: 600;
}

.text-Contact .description-Contact {
    text-align: right;
    /* background-color: blue; */
}